import {LOCAL_TEST, HOST, api_POST} from "./api.js"
let PORT = "";
/**
 * 用于执行登录
 * @param {*} username 用户名
 * @param {*} password 密码
 * @returns 
 */
export const api_register = async function(name, password, phone, company,method){
    if (LOCAL_TEST){
        return {
            flag: true,
            data:{
            }
        }
    }
    let url = "http://"+HOST+PORT+"/retail/customer"
    return await api_POST(url, "register", {name:name, password:password, phone:phone, company:company,method:method});
}