<script>
import {Notify} from "@nutui/nutui";
import {api_login} from "../api/login.js"
export default {
    name: "Login",
    data(){
        return{
            form: {
                username: "",
                password: ""
            }
            
        }
    },
    methods:{
        async submit(){
            if(this.form.username.trim()===""){
                Notify.danger("请输入用户名", {duration: 1000});
                return;
            }
            if(this.form.password.trim()===""){
                Notify.danger("请输入密码", {duration: 1000});
                return;
            }
            let res = await api_login(this.form.username, this.form.password);
            if(res["flag"]===true){
                Notify.success("登录成功");
                localStorage.setItem("id", res["data"]["id"]);
                localStorage.setItem("name", res["data"]["name"]);
                localStorage.setItem("method", res["data"]["method"]);
                this.$router.push("/market");
            }else{
                Notify.danger(res.data);
            }
        }
    },
    async mounted(){
        if(localStorage.getItem("id")!==null){
            this.$router.push("/market");
        }else{
            this.$router.push("/login");
        }
    }
}
</script>

<template>
<div id="body">
    <h1 id="head">登录体验完整功能</h1>
    <p id="subtitle">软件过程与工具大作业项目</p>
    
    
    <div id="space1"></div>
    <nut-input placeholder="请输入用户名"
      v-model="form.username"
       label="用户名"
    />
    <div id="space1"></div>
    <nut-input
        placeholder="请输入密码"
        v-model="form.password"
        type="password"
        label="密码"/>
    

    <div id="to_register">
        没有账号？点击<router-link to="/register">注册</router-link>
    </div>   
    <div id="login">
        <nut-button size="large" type="primary" @click="submit">登录</nut-button>
    </div>
</div>
</template>

<style scoped>
#body{
    position: relative;
    top: 80px;
}

#head{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    width: max-content;
}
#subtitle{
    position: relative;
    left: 60%;
    transform: translate(-50%, 0);
    width: max-content;
    margin-bottom: 60px;
}
#login{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    width: 80%;
}

#space1{
    height: 20px;
}

#to_register{
    margin-top: 20px;
    position: relative;
    left: 70%;
    transform: translate(-50%, 0);
    width: max-content;
    margin-bottom: 60px;
}

</style>