import {LOCAL_TEST, HOST, api_POST} from "./api.js"
let PORT = "";
/**
 * 用于执行登录
 * @param {*} username 用户名
 * @param {*} password 密码
 * @returns 
 */
export const api_login = async function(username, password){
    if (LOCAL_TEST){
        return {
            flag: true,
            data:{
                user_id: 123456
            }
        }
    }
    let url = "http://"+HOST+PORT+"/retail/customer"
    return await api_POST(url, "login", {name:username, password:password});

}

