<script>
import {api_info} from "../api/info.js"
import {Notify} from "@nutui/nutui";
export default {
    data(){
        return{
            user_data:[],
        }
    },
    async mounted(){
        let res = await api_info(localStorage.getItem("name"));
        if(res["flag"]===true){
            this.user_data = res["data"][0];
        }else{
            Notify.error("获取用户信息失败");
        }
        if(localStorage.getItem("id")!==null){
            this.$router.push("/login");
        }
    },
    methods:{
        logout(){
            localStorage.clear();
            this.$router.push("/login");
        },
    }
}
</script>

<template>
<div>
    <nut-avatar id="photo" size="large" icon="https://img12.360buyimg.com/imagetools/jfs/t1/143702/31/16654/116794/5fc6f541Edebf8a57/4138097748889987.png"
    ></nut-avatar>
    <nut-form>
    <nut-form-item label="顾客类型">
        <input class="nut-input-text" placeholder="请输入姓名" type="text"  :value="user_data.method===true?'批发':'零售'" disabled="true" />
    </nut-form-item>
    <nut-form-item label="姓名">
        <input class="nut-input-text" placeholder="请输入姓名" type="text" :value="user_data.name" disabled="true" />
    </nut-form-item>
    <nut-form-item label="联系电话">
        <input class="nut-input-text" placeholder="请输入联系电话" type="text" :value="user_data.phone"  disabled="true"/>
    </nut-form-item>
    <nut-form-item label="单位">
        <input class="nut-input-text" placeholder="请输入单位" type="text" :value="user_data.company"  disabled="true" />
    </nut-form-item>
    <!-- <nut-form-item label="备注">
        <nut-textarea  placeholder="请输入备注" type="text" />
    </nut-form-item> -->
</nut-form>
<div id="logout">
    <nut-button type="danger" @click="logout">退出登录</nut-button>    
</div>
</div>
</template>

<style>
#photo{
    left: 50%;
    transform: translate(-50%, 0);
}
#logout{
    width: max-content;
    position:relative;
    left:50%;
    transform: translate(-50%, 0);

    margin-top: 60px;
}
</style>