import {LOCAL_TEST, HOST, api_POST} from "./api.js"
let PORT = "";
/**
 * 用于搜索客户信息（返回的是一个数组）
 * @param {*} username 用户名
 * @returns 
 */
export const api_info = async function(username){
    if (LOCAL_TEST){
        return {
            flag: true,
            data:{
                 id: 123456,
                name: "阿飞", // 客户姓名
                password: "123213", // 密码
                phone:"12312312312", // 联系方式
                company:"大工哈", // 工作单位
                method:0 // 销售方式, true批发, false零售
            }
        }
    }
    let url = "http://"+HOST+PORT+"/retail/customer"
    return await api_POST(url, "search", {name:username});
}