import {LOCAL_TEST, HOST, api_POST} from "./api.js"
let PORT = "";
/**
 * 用于获取购物车的内容以及编号，一开始和购物车页面都要调用一次
 * @param {*} userid 用户ID
 * @returns 
 */
 export const api_get_cart_by_id = async function(userid){
    if (LOCAL_TEST){
        return {
            flag: true,
            data:{
                id: 23,
                goods: [
                    {
                        id: 1,// 订单项目id
                        goodsId: 0, //货物id
                        goodsName: "汽水",
                        num: 16,
                        discountRate: 1.0,
                    },
                    {
                        id: 2,// 订单项目id
                        goodsId: 3, //货物id
                        goodsName: "包子",
                        num: 4,
                        discountRate: 1.0,
                    },
                ],
                createDate: "20220106",
                imDate: "20220108", //最后修改时间
                price: 645, //总价格
                customerId: 123456,
                profit: 60, //利润
            }
        }
    }
    let url = "http://"+HOST+PORT+"/retail/order"
    return await api_POST(url, "getMyCart", {id:userid});
}

export const api_change_item_in_cart = async function(goodsId, goodsNum, goodsName, cartID){
    if (LOCAL_TEST){
        return {
            flag: true,
            data: null
        }
    }
    let url = "http://"+HOST+PORT+"/retail/order"
    return await api_POST(url, "changeItemNum", {
        goodItems: {
            goodsId: goodsId,
            num: goodsNum,
            discountRate: 1.0,
        },
        orderId: cartID
    });
}

export const api_commit_cart = async function(cartID){
    if (LOCAL_TEST){
        return {
            flag: true,
            data: null
        }
    }
    let url = "http://"+HOST+PORT+"/retail/order"
    return await api_POST(url, "confirm", {
        id: cartID
    });
}