<script>
import {api_search_goods, api_add_cart} from "../api/market.js"
import {Toast, Dialog} from "@nutui/nutui";
import {api_get_orders_by_id, api_pay_order} from "../api/order.js"
export default {

    data(){
        return{
            orderAllList:[],
            orderPayedList: [],
            orderUnPayedList: [],
            orderRefundList: [],
            
            goodsList: [],
            goodsInfoList: [],
            method: false,
            tabIndex: 0,

            columns: [{ title: '订单ID', key: 'id'},{ title: '下单时间', key: 'checkDate'},{ title: '总金额', key: 'price'},],

        }
    },
    methods:{
        async getAllAboutOrders(){
            // 获取所有的商品信息
            let goodsInfoRes = await api_search_goods("");
            let OrdersRes = await api_get_orders_by_id(Number(localStorage.getItem("id")));
            if(OrdersRes["flag"]===true && goodsInfoRes["flag"]===true){
                this.goodsInfoList = goodsInfoRes["data"];
                let orderAllList = OrdersRes["data"];
                let orderPayedList = [];
                let orderUnPayedList = [];
                let orderRefundList = [];
                // 遍历订单项目，构建各自的数据列表。
                for(let i=0; i<orderAllList.length; i++){
                    let orderItem = orderAllList[i];
                    // 将货品信息和订单信息对照起来
                    for(let orderGoodsItemIndex=0; orderGoodsItemIndex<orderItem.goods.length; orderGoodsItemIndex++){
                        let orderGoodsItem = orderItem.goods[orderGoodsItemIndex];
                        let OrderGoodsItemInfo = this.goodsInfoList.filter(function(row){
                            return row.id === orderGoodsItem.goodsId;
                        })[0];
                        orderAllList[i].goods[orderGoodsItemIndex]["price"] = OrderGoodsItemInfo.price;
                        orderAllList[i].goods[orderGoodsItemIndex]["tradePrice"] = OrderGoodsItemInfo.tradePrice;
                        orderAllList[i].goods[orderGoodsItemIndex]["goodsName"] = OrderGoodsItemInfo.name;
                        orderAllList[i].goods[orderGoodsItemIndex]["img"] = OrderGoodsItemInfo.img;
                    }
                    // 将本订单条目插入对应的列表中
                    console.log(orderItem)
                    if(orderItem.pay===0){
                        orderUnPayedList.push(orderItem);
                    }else if(orderItem.refund===0){
                        orderPayedList.push(orderItem);
                    }else{
                        orderRefundList.push(orderItem);
                    }
                }
                this.orderAllList = orderAllList; // 按时间排序嘛
                this.orderUnPayedList = orderUnPayedList.reverse();
                this.orderPayedList = orderPayedList.reverse();
                this.orderRefundList = orderRefundList.reverse();
                // console.log(this.orderUnPayedList);
            }else{
                Toast.fail("获取订单信息失败 "+res["data"]);
            } 

        },
        onRefund(){
            Dialog({
                title: "申请退款",
                content: "您的退款申请正在受理中，请耐心等待结果。"
            });
            // 要注意:v-model="" 和 v-model=""！
        },
        onContact(){
            Dialog({
                title: "联系客服",
                content: "客服平台开发中，联系电话：18834835451"
            });
        },
        async onPay(order_id){
            let res = await api_pay_order(order_id);
            if(res["flag"]===true){
                Toast.success("订单支付成功！");
                this.getAllAboutOrders();
            }else{
                Toast.fail("订单支付失败！");
            }
        }
    },
    async mounted(){
        await this.getAllAboutOrders();
        this.method = localStorage.getItem("method")==="false"?true:false;
        if(localStorage.getItem("id")===null){
            this.$router.push("/login");
        }
    }
}
</script>

<template>
<div>
    <nut-tabs v-model="tabIndex">
    <nut-tabpane title="未支付" >
        <div v-if="orderUnPayedList.length===0" style="margin-top:120px;">
            <nut-empty image="empty" description="这里还没有订单哦！"></nut-empty>
        </div>
        <div class="orderItem"  v-for="orderUnPayedItem in orderUnPayedList" :key="orderUnPayedItem.id">
            <nut-input v-model="orderUnPayedItem.id" :readonly="Boolean(true)"  label="订单ID"/>
            <div style="height:10px;"></div>
            <div v-for="goodsItem in orderUnPayedItem.goods" :key="goodsItem.id">
                <nut-card
                    :img-url="goodsItem.img"
                    :title="goodsItem.goodsName"
                    :price="String(goodsItem.tradePrice)"
                    :vipPrice="String(goodsItem.price)"
                    shopDesc="百年老店"
                    delivery="全校包邮"
                    shopName="工大哈东北总店"
                >
                    <template #prolist>
                        <div class="search_prolist_attr">
                        <span class="word">批发{{String(goodsItem.tradePrice*10/goodsItem.price).slice(0,3)}}折</span>
                        </div>
                    </template>
                </nut-card>
                <div class="ops_row">
                    <h4>数量  {{goodsItem.num}} </h4>
                    <div style="flex:4"></div>
                    <h4>总价</h4>  <nut-price :price="goodsItem.num*(method?goodsItem.price:goodsItem.tradePrice) " size="normal" :need-symbol="true" :thousands="true" />
                </div>
            </div>
            <nut-input v-model="orderUnPayedItem.checkDate" :readonly="Boolean(true)"  label="生成时间"/>
            <nut-input v-model="orderUnPayedItem.price" :readonly="Boolean(true)"  label="总金额"/>
            <div style="height:8px"></div>
            <nut-button type="primary" class="btn_center" @click="onPay(orderUnPayedItem.id)">支付订单</nut-button>
        </div>
    </nut-tabpane>
    <nut-tabpane title="已支付" >
        <div v-if="orderPayedList.length===0" style="margin-top:120px;">
            <nut-empty image="empty" description="这里还没有订单哦！"></nut-empty>
        </div>
        <div class="orderItem" v-for="orderPayedItem in orderPayedList" :key="orderPayedItem.id">
            <nut-input v-model="orderPayedItem.id" :readonly="Boolean(true)"  label="订单ID"/>
            <div style="height:10px;"></div>
            <div v-for="goodsItem in orderPayedItem.goods" :key="goodsItem.id">
                <nut-card
                    :img-url="goodsItem.img"
                    :title="goodsItem.goodsName"
                    :price="String(goodsItem.tradePrice)"
                    :vipPrice="String(goodsItem.price)"
                    shopDesc="百年老店"
                    delivery="全校包邮"
                    shopName="工大哈东北总店"
                >
                    <template #prolist>
                        <div class="search_prolist_attr">
                        <span class="word">批发{{String(goodsItem.tradePrice*10/goodsItem.price).slice(0,3)}}折</span>
                        </div>
                    </template>
                </nut-card>  
                <div class="ops_row">
                    <h4>数量  {{goodsItem.num}} </h4>
                    <div style="flex:4"></div>
                    <h4>总价</h4>  <nut-price :price="goodsItem.num*(method?goodsItem.price:goodsItem.tradePrice) " size="normal" :need-symbol="true" :thousands="true" />
                </div>
            </div>
            <nut-input v-model="orderPayedItem.checkDate" :readonly="Boolean(true)"  label="生成时间"/>
            <nut-input v-model="orderPayedItem.price" :readonly="Boolean(true)"  label="总金额"/>
            <div style="height:8px"></div>
            <nut-button type="primary" class="btn_center" @click="onRefund">申请退款</nut-button>
        </div>
    </nut-tabpane>
    <nut-tabpane title="已退款" >
        <div v-if="orderRefundList.length===0" style="margin-top:120px;">
            <nut-empty image="empty" description="这里还没有订单哦！"></nut-empty>
        </div>
        <div class="orderItem" v-for="orderRefundItem in orderRefundList" :key="orderRefundItem.id">
            <nut-input v-model="orderRefundItem.id" :readonly="Boolean(true)"  label="订单ID"/>
            <div style="height:10px;"></div>
            <div v-for="goodsItem in orderRefundItem.goods" :key="goodsItem.id">
                <nut-card
                    :img-url="goodsItem.img"
                    :title="goodsItem.goodsName"
                    :price="String(goodsItem.tradePrice)"
                    :vipPrice="String(goodsItem.price)"
                    shopDesc="百年老店"
                    delivery="全校包邮"
                    shopName="工大哈东北总店"
                >
                    <template #prolist>
                        <div class="search_prolist_attr">
                        <span class="word">批发{{String(goodsItem.tradePrice*10/goodsItem.price).slice(0,3)}}折</span>
                        </div>
                    </template>
                </nut-card>  
                <div class="ops_row">
                    <h4>数量  {{goodsItem.num}} </h4>
                    <div style="flex:4"></div>
                    <h4>总价</h4>  <nut-price :price="goodsItem.num*(method?goodsItem.price:goodsItem.tradePrice) " size="normal" :need-symbol="true" :thousands="true" />
                </div>
            </div>
            <nut-input v-model="orderRefundItem.checkDate" :readonly="Boolean(true)"  label="生成时间"/>
            <nut-input v-model="orderRefundItem.price" :readonly="Boolean(true)"  label="总金额"/>
            <div style="height:8px"></div>
            <nut-button type="primary" class="btn_center" @click="onContact">联系客服</nut-button>
        </div>
        
    </nut-tabpane>
    </nut-tabs>
    <nut-backtop   :distance="200" :right="10" :bottom="100"></nut-backtop>
    
</div>
</template>

<style>
.ops_row{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
}
.orderItem{
    border-right: 1px gray solid;
    border-bottom: 1px gray solid;
    background-color: whitesmoke;
    padding: 10px;
    border-radius: 8px;
    margin-bottom: 8px;
}
.btn_center{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
}
</style>