<script>
import {api_search_goods, api_add_cart} from "../api/market.js"
import {Toast} from "@nutui/nutui";
import {api_get_cart_by_id} from "../api/cart.js"
export default {

    data(){
        return{
            searchValue: "",
            goodsList: [],
        }
    },
    methods:{
        async onSearch(){
            let res = await api_search_goods(this.searchValue);
            if(res["flag"]===true){
            this.goodsList = res["data"];
            }else{
                Toast.fail("未查询到任何商品");
            }
        },
        async addCart(id, gname){
            let res = await api_add_cart(
                id,
                1,
                gname,
                localStorage.getItem("cart_id"));
            if(res["flag"]===true){
                Toast.success("添加购物车成功",{duration:400});
            }else{
                Toast.fail("添加失败 "+res["data"]);
            }
        }
    },
    async mounted(){
        if(localStorage.getItem("id")===null){
            this.$router.push("/login");
        }
        let res = await api_search_goods("");
        if(res["flag"]===true){
            this.goodsList = res["data"];
        }else{
            Toast.fail("未查询到任何商品");
        }

        let cartInfo = await api_get_cart_by_id(Number(localStorage.getItem("id")));
        if(cartInfo["flag"]===true){
            console.log("获取用户购物车",cartInfo);
            localStorage.setItem("cart_id",cartInfo["data"]["id"]);
        }else{
            Toast.fail("获取购物车失败");
        }
    },
    watch:{
        searchValue(newValue, oldValue){
           if(newValue.trim()===""){
               this.onSearch();
           }
        }
    },
}
</script>

<template>
<div>
    <nut-searchbar v-model="searchValue" placeholder="搜索商品" @search="onSearch">
        <template v-slot:leftin>
            <nut-icon size="14" name="search2"></nut-icon>
        </template>
    </nut-searchbar>
    
    <div>
        <nut-swiper :init-page="0" :pagination-visible="true" pagination-color="#426543" auto-play="3000" height="200">
        <nut-swiper-item>
            <img src="https://img2.baidu.com/it/u=1189085500,4133594552&fm=253&fmt=auto&app=120&f=JPEG?w=200&h=200" alt="" />
        </nut-swiper-item>
        <nut-swiper-item>
            <img src="https://img1.baidu.com/it/u=961597277,2375562680&fm=253&fmt=auto&app=120&f=JPEG?w=200&h=200" alt="" />
        </nut-swiper-item>
        <nut-swiper-item>
            <img src="https://img1.baidu.com/it/u=2779013017,1184228350&fm=26&fmt=auto" alt="" />
        </nut-swiper-item>
    </nut-swiper>
    </div>
    <div>

        <nut-card
            v-for="goodsItem in goodsList"
            :key="goodsItem.id"
            :img-url="goodsItem.img"
            :title="goodsItem.name"
            :price="String(goodsItem.tradePrice)"
            :vipPrice="String(goodsItem.price)"
            shopDesc="百年老店"
            delivery="全校包邮"
            shopName="工大哈东北总店"
            >
            <!-- {{goodsItem}} -->
            <template #prolist>
                <div class="search_prolist_attr">
                <span class="word">批发{{String(goodsItem.tradePrice*10/goodsItem.price).slice(0,3)}}折</span>
                </div>
            </template>
        
            <template #footer>
                <nut-button type="primary"  @click="addCart(goodsItem.id, goodsItem.name);">加入购物车</nut-button>
            </template>
        </nut-card>
        <nut-backtop   :distance="200" :right="10" :bottom="100"></nut-backtop>
    </div>
    
</div>
</template>

<style>
.word{
    font-size: 12px;
    border: 1px wheat solid;
    background-color: wheat;
}
</style>