export const api_add_cart = async function(goodsId, num, goodsName, orderId){
    if (LOCAL_TEST){
        return {
            flag: true,
            data: null
        }
    }
    let url = "http://"+HOST+PORT+"/retail/order"
    return await api_POST(url, "changeItemNum", {
        goodItems:{
            goodsId: goodsId,  //货品ID
            num: -1, //货品数量
            discountRate: 1.0,
        },
        orderId: orderId, //购物车ID
    });
}