<script>
export default {
    data(){
        return{
            NavNameList:{
                "/market":"商城",
                "/cart":"购物车",
                "/order":"订单",
                "/info":"我的"
            },
            NavName: "商城",
        };
    },
    watch:{
        $route(to, from){
            this.NavName = this.NavNameList[to.path];
        }
    }
}

</script>

<template>
    <div>
        <h2 >{{ NavName }}</h2>
    </div>
</template>

<style scoped>
    div{
        border-radius: 4px;
    }
    h2{
        position: absolute;
        left: 10px;
        top: 8px;
    }
</style>