<script>
import {api_get_cart_by_id, api_change_item_in_cart, api_commit_cart} from "../api/cart.js"
import {api_search_goods, api_add_cart} from "../api/market.js"
import {Toast} from "@nutui/nutui";
export default {

    data(){
        return{
            goodsList: [],
            goodsInfoList:[],
            UserMethod: true,
            totalPrice: 0
        }
    },
    watch:{
    },
    methods:{
        async onChangeItemNum(goodsId, goodsNum, goodsName, index){
            let res = await api_change_item_in_cart(goodsId, goodsNum, goodsName, Number(localStorage.getItem("cart_id")));
            if(res["flag"]===true){
                this.goodsList[index].num = Number(goodsNum);
            }else{
                Toast.fail("购买数量修改失败");
                this.getAllAboutCart();
            }
            this.countTotalPrice();
        },
        countTotalPrice(){
            let total = 0;
            for(let i=0; i<this.goodsList.length; i++){
                let goodsItemInCart = this.goodsList[i]; // 一条商品信息
                total += goodsItemInCart.num * (this.UserMethod?goodsItemInCart.tradePrice:goodsItemInCart.price);
            }
            this.totalPrice = total;
        },
        async getAllAboutCart(){
            let res = await api_get_cart_by_id(Number(localStorage.getItem("id")));
            
            if(res["flag"]===true){
                this.goodsList = res["data"]["goods"];
            }else{
                Toast.fail('获取购物车失败，请稍后再试');
            }
            let goodsInfoRes = await api_search_goods("");
            if(goodsInfoRes["flag"]===true){
                
                this.goodsInfoList = goodsInfoRes["data"];
                // 将购物车信息和商品信息合并
                // console.log("this.goodsList", this.goodsList);
                // console.log("goodsInfoList", this.goodsInfoList);
                
                for(let i=0; i<this.goodsList.length; i++){
                    let goodsItemInCart = this.goodsList[i]; // 一条商品信息
                    
                    let goodsItemInCartInfo = this.goodsInfoList.filter(function(row){
                        
                        return row.id === goodsItemInCart.goodsId;
                    })[0];
                    this.goodsList[i]["price"] = goodsItemInCartInfo.price;
                    this.goodsList[i]["tradePrice"] = goodsItemInCartInfo.tradePrice;
                    console.log(goodsItemInCartInfo)
                    this.goodsList[i]["goodsName"] = goodsItemInCartInfo.name;
                    this.goodsList[i]["img"] = goodsItemInCartInfo.img;
                }
            }else{
                Toast.fail("获取商品信息失败...");
            }
            this.UserMethod = JSON.parse(localStorage.getItem("method"));
            this.countTotalPrice();
        },
        async onDeleteItemFromCart(goodsId, goodsName){
            let res = await api_change_item_in_cart(goodsId, 0, goodsName,Number(localStorage.getItem("cart_id")));
            if(res["flag"]===true){
                this.goodsList = this.goodsList.filter(function(row){
                    return row.goodsId !== goodsId;
                });
                
            }else{
                Toast.fail("删除修改失败");
                this.getAllAboutCart();
            }
            this.countTotalPrice();
        },
        async onSubmitCart(){
            let res = await api_commit_cart(Number(localStorage.getItem("cart_id")));
            if(res["flag"]===true){
                Toast.success("订单创建成功");
                let cartInfo = await api_get_cart_by_id(Number(localStorage.getItem("id")));
                if(cartInfo["flag"]===true){
                    console.log("获取用户购物车",cartInfo);
                    localStorage.setItem("cart_id",cartInfo["data"]["id"]);
                }else{
                    Toast.fail("获取购物车失败");
                }
            }else{
                Toast.fail("订单创建失败");
            }
            this.getAllAboutCart();
            this.countTotalPrice();
        }
    },
    async mounted(){
        await this.getAllAboutCart();
        if(localStorage.getItem("id")===null){
            this.$router.push("/login");
        }
   }
}
</script>

<template>
    <div>
        <div id="note">
            <nut-noticebar :closeMode="true">
            《软件过程与工具》新品即将上市，活动期间0元预约可参与抽奖，赢《人月神话》等好礼，更多产品信息请持续关注！
            </nut-noticebar>
        </div>
        <div v-for="(goodsItem, index) in goodsList" :key="goodsItem.id">
            <!-- {{goodsItem}} -->
            <nut-card
                :img-url="goodsItem.img"
                :title="goodsItem.goodsName"
                :price="String(goodsItem.tradePrice)"
                :vipPrice="String(goodsItem.price)"
                shopDesc="百年老店"
                delivery="全校包邮"
                shopName="工大哈东北总店"
            >
                <template #prolist>
                    <div class="search_prolist_attr">
                    <span class="word">批发{{String(goodsItem.tradePrice*10/goodsItem.price).slice(0,3)}}折</span>
                    </div>
                </template>
                <template #footer>
                    <nut-button type="danger" @click="onDeleteItemFromCart(goodsItem.goodsId, goodsItem.goodsName);">删除商品</nut-button>    
                </template>
            </nut-card>
            <div class="ops_row">
                <h3 >数量</h3>
                <nut-inputnumber  v-model="goodsItem.num"  button-size="30" input-width="50"
                    @add="onChangeItemNum(goodsItem.goodsId, goodsItem.num+1, goodsItem.goodsName, index);"
                    @reduce="onChangeItemNum(goodsItem.goodsId, goodsItem.num-1, goodsItem.goodsName, index);"
                     readonly
                />
            
            <h3>单价</h3><nut-price :price="UserMethod?goodsItem.tradePrice:goodsItem.price" size="normal" :need-symbol="true" :thousands="true" />
            <h3>总价</h3><nut-price :price="goodsItem.num * (UserMethod?goodsItem.tradePrice:goodsItem.price) " size="normal" :need-symbol="true" :thousands="true" />
            </div>
        </div>
        <div v-if="goodsList.length===0" style="margin-top:120px;">
            <nut-empty image="empty" description="还没有商品，快去逛逛商城吧！"></nut-empty>
        </div>
        <nut-backtop   :distance="200" :right="10" :bottom="100"></nut-backtop>
        <div class="ops_buttom_row">
            <h3>总计</h3>
            <div id="total_price">
                <nut-price :price="totalPrice" size="normal" :need-symbol="true" :thousands="true" />
            </div>
            <div style="flex:3"></div>
            <div id="submit_order">
                <nut-button type="primary" @click="onSubmitCart">结算</nut-button>  
            </div>
            <div style="flex:1"></div>
        </div>
    </div>
</template>

<style>
.ops_row{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
}
.ops_buttom_row{
    position: fixed;
    display: flex;
    flex-direction: row;
    bottom: 50px;
    background-color: white;
    width: 100%;
    align-items: center;
    justify-content: center;
}
.ops_buttom_row h3{
    display: inline-flex;
    flex:2;
    margin-left: 30px;
}

.total_price{
    display: inline-flex;
    flex:1;
}

.submit_order{
    flex: 2;
}

#note{
    display: block;
    height: 40px;
}

</style>