<script>
import {Notify} from "@nutui/nutui";
import {api_register} from "../api/register.js"

export default {
    name: "Register",
    data(){
        return{
            // name, password, phone, company,method
            form: {
                username: "",
                password: "",
                password2:"",
                phone: "",
                company: "",
                method: ""
            },
            showChoices: false,
            types:["零售", "批发"]
        
        }
    },
    methods:{
        async submit(){
            if(this.form.username.trim()===""){
                Notify.danger("请输入用户名", {duration: 1000});
                return;
            }
            if(this.form.password.trim()===""){
                Notify.danger("请输入密码", {duration: 1000});
                return;
            }
            if(this.form.password.trim()===""||
                this.form.password.trim()!==this.form.password2.trim()
            ){
                Notify.danger("两次密码输入不一致", {duration: 1000});
                return;
            }
            let user_type = this.form.method==="零售"? false: true;
            let res = await api_register(this.form.username, this.form.password, this.form.phone, this.form.company, user_type);
            if(res["flag"]===true){
                Notify.success("注册成功");
                this.$router.push("/login");
            }else{
                Notify.danger(res["data"]);
            }

        },
        onChooseType(){
            this.showChoices = true;
        },
        confirm(res){
            this.form.method = res;
        }
    },
    mounted(){
        
    }
}
</script>

<template>
<div id="body">
    <h1 id="head">用户注册</h1>
    <p id="subtitle">软件过程与工具大作业项目</p>
    <nut-input class="input_row"
      placeholder="请输入用户名"
      v-model="form.username"
       label="用户名"
    />
    <nut-input class="input_row"
        placeholder="请输入密码"
        v-model="form.password"
        type="password"
        label="密码"/>
    <nut-input class="input_row"
        placeholder="请再次输入密码"
        v-model="form.password2"
        type="password"
        label="确认密码"/>
    <nut-input class="input_row"
        placeholder="请输入电话号码"
        v-model="form.phone"
        label="电话号码"/>
    <nut-input class="input_row"
        placeholder="请输入所在单位"
        v-model="form.company"
        label="所在单位"/>
    
    <nut-input class="input_row"
        placeholder="选择客户类型"
        v-model="form.method"
        @focus="onChooseType"
        label="客户类型">
    </nut-input>
    <nut-picker
        v-model:visible="showChoices"
        :list-data="types"
        title="客户类型选择"
        @confirm="confirm" 
        :default-value-index="1"
    ></nut-picker>
    

    <div id="to_login">
        已有账号？点击<router-link to="/login">登录</router-link>   
    </div>
    <div id="register">
        <nut-button size="large" type="primary" @click="submit">注册</nut-button>
    </div>


    

</div>
</template>

<style scoped>
#body{
    position: relative;
    top: 80px;
}

#head{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    width: max-content;
}
#subtitle{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    width: max-content;
    margin-bottom: 60px;
}
#register{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    width: 80%;
}

#space1{
    height: 20px;
}

#to_login{
    margin-top: 20px;
    position: relative;
    left: 70%;
    transform: translate(-50%, 0);
    width: max-content;
    margin-bottom: 60px;
}
.input_row{
    margin-top: 4px;
    margin-bottom: 4px;
}

#type{
    position: relative;
    left: 50%;
    transform: translate(-50%, 0);
    width: max-content;

}
</style>