export const LOCAL_TEST = false;
export const HOST = "192.168.1.108:8081"
import axios from "axios"
/**
 * 将数据传到后端
 * @param {*} action 行为
 * @param {*} data 发送的数据 
 */
export const api_POST = async function(url, action, data){
    let response;
    await axios({
        method: "post",
        url:url,
        headers:{
            "Content-Type": "application/x-www-form-urlencoded"
        },
        data:"action="+action+"&data="+JSON.stringify(data)
    }).then(function(res){
        response = res["data"];
    });
    return response;
}