<script>
export default {
    data(){
        return {
            navRoutes : {
                0: "/market",
                1: "/cart",
                2: "/order",
                3: "/info"
            }
        }
    },
    methods:{
        tabSwitch(item, index){
            this.$router.push(this.navRoutes[index]);
        },
    }
}
</script>

<template> 
    <nut-tabbar :buttom="true" @tab-switch="tabSwitch">
        <nut-tabbar-item tab-title="商城" icon="home"></nut-tabbar-item>
        <nut-tabbar-item tab-title="购物车" icon="cart"></nut-tabbar-item>
        <nut-tabbar-item tab-title="订单" icon="category" ></nut-tabbar-item>
        <nut-tabbar-item tab-title="我的" icon="my"></nut-tabbar-item>
    </nut-tabbar>
</template>

